import React from 'react'
import {TabBar} from 'antd-mobile'
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        
      }
    
    render() {
        const {pathname} = this.props.location;
        return (
          <div style={ { position: 'fixed', width: '100%', bottom: 0 }}>
            <TabBar
              unselectedTintColor="#949494"
              tintColor="#33A3F4"
              barTintColor="white"
            >
              <TabBar.Item
                title="首页"
                key="home"
                icon={
                    <i className='iconfont icon-Homehomepagemenu'></i>
                }
                selectedIcon={
                    <i className='iconfont icon-Homehomepagemenu1'></i>
                }
                selected={pathname === '/'}
                onPress={() => {
                  this.props.history.push('/');
                }}
                data-seed="logId"
              >
              </TabBar.Item>
              <TabBar.Item
                icon={
                  <i className='iconfont icon-shoucang1'></i>
                }
                selectedIcon={
                  <i className='iconfont icon-star__easyico'></i>
                }
                title="收藏"
                key="shoucang"
                selected={pathname === '/shoucang'}
                onPress={() => {
                 this.props.history.push('/shoucang')
                }}
                data-seed="logId1"
              >
              </TabBar.Item>
              <TabBar.Item
                icon={
                  <i className='iconfont icon-List' ></i>
                }
                selectedIcon={
                  <i className='iconfont icon-Listmenuoptions'></i>  
                }
                title="订单列表"
                key="list"
                selected={pathname === '/list'}
                onPress={() => {
                  this.props.history.push('/list')
                }}
              >
              </TabBar.Item>
              <TabBar.Item
                icon={<i className='iconfont icon-wode'></i>}
                selectedIcon={
                  <i className='iconfont icon-wodedangxuan'></i>
                }
                title="我的淘宝"
                key="mypage"
                selected={pathname === '/Mypage'}
                onPress={() => {
                  this.props.history.push('/Mypage')
                }}
              >
              </TabBar.Item>
            </TabBar>
          </div>
        );
    }
}
export default withRouter(Mytab);