import React, { Component } from 'react'
import { SearchBar, } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import Mycarousel from './Mycalerseol'
import './home.css'
import { connect } from 'react-redux';


class Home extends React.Component{
    constructor(){
        super();
    }
    componentDidMount(){
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist'
        fetch(url,{
            method:'get',
            header:{'content-Type':'application/json'}
        })
        .then(res=>res.json())
        .then(myjson=>{
            this.props.dispatch({
                type:'GET_DATA',
                datalist:myjson.data
            })
        })
    }
    
    render(){
            return (
            <div>
              <SearchBar placeholder="搜索店铺宝贝" maxLength={8} onFocus={()=>{this.props.history.push('/search')}}/>  
              <Mycarousel />
              {     
                  this.props.datalist.map((item,i)=>{
                    
                      return(
                            <div className='list' key={i} 
                            onClick={()=>
                                {this.props.history.push({pathname:`/details/${item.id}`})
                            }}>
                                <img src={item.img} />
                                <p>{item.title}</p>
                                <p>￥{item.price}.00</p>
                            </div>
                      )
                  })
                
              }
              <div className='empty'> </div>
            </div>
        );
    }
}


const  mapStateToProps=(state)=>{
    return{
        datalist:state.getdataRuducer.datalist
    }
}
export default withRouter(connect(mapStateToProps)(Home))

