import React from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom';
import './shoucang.css'


class Shoucang extends React.Component{
    constructor(props){
        super(props);
        this.state=[]
    }
    
    islogin=()=>{
        if(!this.props.userinfo){
            this.props.history.push('/login');
        }
    }
    render(){
        let shoucanglist =[];
        this.props.shoucang.map(item=>{
            shoucanglist.push(this.props.datalist.filter(i=>i.id==item)[0]);
        })
        return(
            <div>
            {
                this.islogin(),
                shoucanglist.map((item,i)=>{
                    return(
                        <div key={i}>
                            <div className='shoucanglist'>
                                <div><img className='shoucangimg' src={item.img} /></div>
                                <div><p className='shoucangt'>{item.title}</p></div>
                                <div><p className='shoucangp'>￥{item.price}.00</p></div>
                                <div><button onClick={()=>{
                                    this.props.dispatch({
                                        type:'DEL_SC',
                                        index:i,
                                        shoucang:shoucanglist[i].id
                                    });
                                }}>删除</button></div>
                            </div>
            
                        </div>
                    )
                })
            }
        </div>
        )
        }
    }


const mapStateToProps=(state)=>{
    return{
        datalist:state.getdataRuducer.datalist,
        shoucang:state.SCReducer.shoucang,
        userinfo:state.loginReudcer.userinfo
    }
}
export default withRouter(connect(mapStateToProps)(Shoucang))