import React from 'react'
import { withRouter } from 'react-router-dom';
import './details.css'
import {add_sc} from './actionCreators'
import { connect } from 'react-redux';


class Details extends React.Component{
    constructor(){
        super();
        this.state={sc:'icon-shoucang'}
    }
    componentDidMount(){
        let shoucangsc = this.props.shoucang
        let dataid = this.props.location.pathname.split('/')[2];
        for(let i =0 ;i<shoucangsc.length;i++){
            if(shoucangsc[i]===dataid){
                this.setState({sc:'icon-star__easyico'})
            }
        }
    }
    addsc=()=>{
        
        if(!this.props.userinfo){
            this.props.history.push('/login')
        }else{
            if(this.state.sc==='icon-shoucang'){
                this.props.dispatch({
                        type:'ADD_SC',
                        shoucang:this.props.location.pathname.split('/')[2]
                }
                );
                this.setState({
                    sc:'icon-star__easyico'
                })
            }else{
                alert('您选择的商品已经被收藏！')
            }
            
        }
    }
    render(){
        let dataid = this.props.location.pathname.split('/')[2];
        let data = this.props.datalist.filter((item)=>item.id == dataid)[0];
        return(
        <div className='details'>
            <img src={data.img}></img>
            <p className='num'>￥{data.price}.00</p>
            <p className='text'>{data.title}</p>
            <i className='iconfont icon-fanhui' id='fanhui'
            onClick={()=>{
                this.props.history.push('/');
            }}></i>
            <ul className='buttonbar'>
                <button className='but'>
                    <i className='iconfont icon-dianpu'></i>
                    <p>店铺</p>
                </button>
                <button className='but'>
                    <i className='iconfont icon-kefu'></i>
                    <p>客服</p>
                </button>
                <button className='but1' 
                // onClick={this.addone}
                onClick={this.addsc}
                >
                    <i className={`iconfont ${this.state.sc}`}></i>
                    <p>收藏</p>
                </button>
                <button className='addcart' >
                    加入购物车
                </button>
    
                <button className='tobuy'>
                    立即购买
                </button>
            </ul>
        </div>
        )
    }
}
const  mapStateToProps=(state)=>{
    return{
        datalist:state.getdataRuducer.datalist,
        shoucang:state.SCReducer.shoucang,
        userinfo:state.loginReudcer.userinfo
    }
}
export default withRouter(connect(mapStateToProps)(Details))



