
const loginReudcer = (state={userinfo:''},action)=>{
    if(action.type == 'LOGIN'){
        return {userinfo:action.userinfo}
    }else{
        return state;
    }
}
const SCReducer = (state={shoucang:[]},action)=>{
    switch(action.type){
        case 'ADD_SC':
            state.shoucang.push(action.shoucang);
            return state;
        case 'DEL_SC':
            state.shoucang.splice(action.index,1);
            // console.log(state);
            return {shoucang:state.shoucang};
        default:
            return state;
    }

}
const getdataRuducer =(state = {datalist:[]},action)=>{
    switch(action.type){
        case 'GET_DATA':
            return {datalist:action.datalist}
        default:
            return state;
    }
}
export  {loginReudcer,getdataRuducer,SCReducer};



