
import React from 'react'
import { Carousel, WingBlank } from 'antd-mobile';

class Mycarousel extends React.Component {
  state = {
    data: ['1', '2', '3'],
    imgHeight: 180,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['140/O1CN01i5HblQ1CuA4mpYz96_!!140-0-lubanu', '1/O1CN01TgPhpb1BsV1d0nG8U_!!1-0-lubanu', '141/O1CN01UGVSzV1CucTWZoxCY_!!141-0-lubanu'],
      });
    }, 100);
  }
  render() {
    return (
      // <WingBlank>
        <Carousel
          autoplay={true}
          infinite
        >
          {this.state.data.map(val => (
              <img
                src={`https://gw.alicdn.com/imgextra/i1/${val}.jpg`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
          ))}
        </Carousel>
      // </WingBlank>
    );
  }
}
export default Mycarousel